.container {
    padding: 25px 42px 37px;
}

.cardContainer {
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    align-items: stretch;
}

.cardContainer > div {
    flex: 0 0 33.3333%
}

.cardContainer > div {
    flex: 0 0 calc(33.3333% - 26.6666px);
    margin-right: 40px;
}

.cardContainer > div:last-child {
    margin-right: 0;
}

.card {
    position: relative;
    flex: 1 0 auto;
    overflow: hidden;
    max-height: 83vh;
}

.cardHeadSpacer {
    padding: 24px;
}

.cardHeadSpacer > h3 {
    font-weight: 450;
    font-size: 24px;
    line-height: 1.2803;
    letter-spacing: 0.015em;
    color: var(--inputLineColor);
}